// @flow
import React, { Component } from 'react';
import { select } from 'd3-selection';
import debounce from 'just-debounce-it';

import VideoWaypoint from '../Waypoints/Videos/VideoQuotesSideBySide/VideoWaypoint';

import ScrollyDivWithFixedVideo from '@metro-prez/scrolly-div-with-fixed-video';

import './App.css';
import './ArticleSection.css';
import './Footer.css';
import {
  isTablet,
  isMobile
} from '../../lib/ffx-prez-common-utilities/ffx-prez-common-utilities';


type State = {
  screenSize: string,
  masthead: string,
  link: string,
  title: string,
  description: string,
  activeMapLayer?: string
};


class App extends Component<ArticleConfig, State> {
  constructor(props: ArticleConfig) {
    super(props);

    this.state = {
      screenSize: isMobile() ? 'small' : isTablet() ? 'medium' : 'large',
      mapIsFixed: false,
      activeMapLayer: undefined
    };
  }


  componentDidMount = () => {
    select(window).on(
      'resize.App',
      debounce(this.checkForChangeInScreenSize, 300)
    );
  };


  checkForChangeInScreenSize = () => {
    const screenIsSmall = isMobile();
    const screenIsMedium = isTablet();
    const { screenSize } = this.state;
    if (screenIsSmall && screenSize !== 'small') {
      this.setState({ screenSize: 'small' });
    } else if (screenIsMedium && screenSize !== 'medium') {
      this.setState({ screenSize: 'medium' });
    } else if (!screenIsSmall && !screenIsMedium && screenSize !== 'large') {
      this.setState({ screenSize: 'large' });
    }
  };


  
  componentWillDismount = () => {
    if (typeof window !== 'undefined') {
      select(window).on('resize.App', null);
    }
  };



  createVideoWaypoint = (data) => {
    let itemList = data.map((item, i) => {
        return (
          <VideoWaypoint key={i} data={item} />
        )
    });

    return (
      <React.Fragment> {itemList} </React.Fragment>
    )
  };



  render() {
    const {
      waypointDataVideoQuotes
    } = this.props;

    return (
      <div className="app">

        <div className="article-section">
          <div className="article-section__subsection">
         
            <p>
              <span className="drop-cap">A</span>s the sky darkened and the
              black smoke thickened, a rumbling noise like the sound of jumbo
              jet engines began to crescendo.
            </p>
            <p>
              Within minutes, embers from the huge Kilmore East fire were
              raining down on dry bushland as flames up to 100 metres high
              licked the treetops.
            </p>
            <p>
              In just 12 hours, the catastrophic blaze would descend on dozens
              of communities to the north-east of Melbourne, claiming 119 lives
              and destroying thousands of homes.
            </p>
            <p>
              It was February 7, 2009, and the Kilmore East fire was one of 15
              major devastating blazes that rocked Victoria on what was to
              become known as Black Saturday.
            </p>

            <p>
              In just 12 hours, the catastrophic blaze would descend on dozens
              of communities to the north-east of Melbourne, claiming 119 lives
              and destroying thousands of homes.
            </p>

            <p>
              In just 12 hours, the catastrophic blaze would descend on dozens
              of communities to the north-east of Melbourne, claiming 119 lives
              and destroying thousands of homes.
            </p>
            <p>Here’s how that fire spread.</p>
          </div>
        </div>

        
        {/* {this.createVideoWaypoint(scrollyData)} */}
        <ScrollyDivWithFixedVideo  data={waypointDataVideoQuotes[0]} />
        {/* <MapWayPoint  /> */}


        <div className="article-section">
          <div className="article-section__subsection">
            <p>
              Those who witnessed the inferno described how quickly it descended
              on their homes and townships, in particular after a south-westerly
              wind change saw the fire become erratic as it shifted direction.
            </p>
            <p>
              One St Andrews resident said that by 3.30pm the sky was “orange
              with smoke” as the walls of her home began to vibrate. She ran to
              her car and fled.
            </p>
            <p>
              “One minute it was clear and the next there were flames
              everywhere,” she told the 2009 Victorian Bushfires Royal
              Commission. “There just seemed to be fire all around us on both
              sides of the road.”
            </p>
          </div>
        </div>

        <ScrollyDivWithFixedVideo  data={waypointDataVideoQuotes[1]} />


        <div className="article-section">
          <div className="article-section__subsection">
        
            <p>
              More than an hour later, in Steels Creek, where 10 people died,
              the fire front morphed in front of resident David Gormley.
            </p>
            <p>
              “The black ash started to turn to glowing embers,” he told the
              royal commission, “which then turned into flaming embers and large
              chunks of debris that were burning. The wind was gale force, the
              rumbling was deafening and it was pitch black.”
            </p>
            <p>
              Those in their homes said it was like “being inside a washing
              machine on spin cycle, and full of fire and embers”.
            </p>

            <p>
              More than an hour later, in Steels Creek, where 10 people died,
              the fire front morphed in front of resident David Gormley.
            </p>
            <p>
              “The black ash started to turn to glowing embers,” he told the
              royal commission, “which then turned into flaming embers and large
              chunks of debris that were burning. The wind was gale force, the
              rumbling was deafening and it was pitch black.”
            </p>
            <p>
              Those in their homes said it was like “being inside a washing
              machine on spin cycle, and full of fire and embers”.
            </p>

            <p>
              More than an hour later, in Steels Creek, where 10 people died,
              the fire front morphed in front of resident David Gormley.
            </p>
            <p>
              “The black ash started to turn to glowing embers,” he told the
              royal commission, “which then turned into flaming embers and large
              chunks of debris that were burning. The wind was gale force, the
              rumbling was deafening and it was pitch black.”
            </p>
            <p>
              Those in their homes said it was like “being inside a washing
              machine on spin cycle, and full of fire and embers”.
            </p>

            <p>
              More than an hour later, in Steels Creek, where 10 people died,
              the fire front morphed in front of resident David Gormley.
            </p>
            <p>
              “The black ash started to turn to glowing embers,” he told the
              royal commission, “which then turned into flaming embers and large
              chunks of debris that were burning. The wind was gale force, the
              rumbling was deafening and it was pitch black.”
            </p>
            <p>
              Those in their homes said it was like “being inside a washing
              machine on spin cycle, and full of fire and embers”.
            </p>
          </div>
        </div>


      </div>
    );
  }
}

export default App;



