import React from 'react';

const Spacer = ({ color }) => {
  return (
    <div id="spacer"
      style={{
        height: '100vh',
        opacity: 0.8,
        background: color,
        position: 'relative',
        zIndex: 100,
        pointerEvents: 'none'
      }}
    />
  );
};

export default Spacer;



