
// @flow

import React, { Component } from 'react';
import {Waypoint} from 'react-waypoint';
import DVideo from '../DVideo';
import Spacer from '../../../../App/Spacer';
import './VideoWaypoint.css';
import {
  isTablet,
  isMobile
} from '../../../../../lib/ffx-prez-common-utilities/ffx-prez-common-utilities';



const ScrollyNotePullQuote = ({ name, role, children, customStyles, customPosition }) => (
  <div className="app__scrolly-map-container__content" style={customStyles}>
    <div className="app__scrolly-map-container__content__note" style={customPosition}>
      <div className="app__scrolly-map-container__content__note__caption">
        <span>{name}</span> {name !== undefined ? role : null}
      </div>

      <div className="app__scrolly-map-container__content__note__pullquote">
        {children}
      </div>
    </div>
  </div>
);





class VideoWaypoint extends Component{

  constructor(props) {

      super(props);

      this.state = {
        screenSize: isMobile() ? 'small' : isTablet() ? 'medium' : 'large',
        divIsFixed: false,
        activeLayer: undefined
      };

  }




  componentDidMount=()=>{
     this.prepareScrollyStageFunctions();
  }





  scrollyStageFunctions = { enter: {}, leave: {} };


  prepareScrollyStageFunctions=()=>{

    let scrollyStages = [];

    for(let i = 0; i< this.props.data.waypoints.length; i++){
      scrollyStages.push(this.props.data.waypoints[i].name);
    }

    this.scrollyStageFunctions = { enter: {}, leave: {} };

    scrollyStages.forEach((scrollyStage, i) => {
      this.scrollyStageFunctions.enter[scrollyStage] = this.onEnterScrollyStage(
        scrollyStage,
        i === 0 ? undefined : scrollyStages[i - 1]
      );
    });

    scrollyStages.forEach((scrollyStage, i) => {
      if (i !== 0) {
        this.scrollyStageFunctions.leave[
          scrollyStage
        ] = this.onLeaveScrollyStage(scrollyStage, scrollyStages[i - 1]);
      }
    });
  }


  //
  // Map position scrolly-telling functions
  // ---------------------------------------------------------------------

  onEnterMapFromTop = ({ previousPosition, currentPosition, event }) => {
    if (previousPosition === 'below') {
      this.setState({ divIsFixed: true, divIsAtBottom: false });
    }
  };

  onEnterMapFromBottom = ({ previousPosition, currentPosition, event }) => {
    if (previousPosition === 'inside' && currentPosition === 'below') {
      this.setState({ divIsFixed: true, divIsAtBottom: false });
    }
  };

  onExitMapFromTop = ({ previousPosition, currentPosition, event }) => {
    if (previousPosition === 'inside' && currentPosition === 'below') {
      this.setState({ divIsFixed: false, divIsAtBottom: false });
    }
  };

  onExitMapFromBottom = ({ previousPosition, currentPosition, event }) => {
    if (previousPosition === 'below' || event === null) {
      this.setState({ divIsFixed: false, divIsAtBottom: true });
    }
  };

  //
  // Map element scrolly-telling functions
  // ---------------------------------------------------------------------

  onEnterScrollyStage = (scrollyStage, previousScrollyStage) => {
    return ({ previousPosition, currentPosition, event }) => {
    
      if (previousPosition === 'below') {
        this.setState({
          activeLayer: scrollyStage
        });
      } else if (event === null) {
        this.setState({
          activeLayer: scrollyStage,
          divIsFixed: true,
          divIsAtBottom: false
        });
      }
    };
  };





  onLeaveScrollyStage = (scrollyStage, previousScrollyStage) => {
    return ({ previousPosition, currentPosition, event }) => {
      if (previousPosition === 'inside' && currentPosition === 'below') {
        this.setState({
          activeLayer: 
            previousScrollyStage !== undefined
              ? previousScrollyStage
              : scrollyStage
        });
      }
    };
  };



  createScrollyNotes = (data) => {
    const {activeLayer} = this.state;

    let itemList = data.waypoints.map((item, i) => {
          return (

            <Waypoint
                key = {item.name}
                onEnter={this.scrollyStageFunctions.enter[item.name]}
                onLeave={this.scrollyStageFunctions.leave[item.name]}
              >
                <div>
                  <ScrollyNotePullQuote
                    key={item.note.name}
                    name={item.note.name}
                    role={item.note.role}
                    customStyles={{ opacity: activeLayer === item.name ? 1 : 0.4 }}
                    customPosition={{right: data.position === 'right'? '46px' : 'inherit' }}
                  >
                    {item.note.quote}

                  </ScrollyNotePullQuote>
                </div>
             
            </Waypoint>

          )
      });

      return (
        <React.Fragment >
          {itemList} 
        </React.Fragment>

      )
  };




  createWaypoints = (data) => {

      return (

        <Waypoint
            key={data.layer}
            onEnter={this.scrollyStageFunctions.enter[data.layer]}
            onLeave={this.onLeave1200FromTop}
            topOffset='10%'
          >
          <div>
          {this.createScrollyNotes(data)}
          </div>

        </Waypoint>

      )
  };





  

  render() {

    if(!this.props.data) return null;

    const{data} = this.props;

    const {
      screenSize,
      divIsFixed,
      divIsAtBottom,
      activeLayer
    } = this.state;



    const videoPlayData = {
      source: data.videoItem //{poster: "videos/marktaylor_poster.png", video: "videos/marktaylor.mp4"}
    };



    const playFramesOnScroll = false;

    return (


      <div className="app__scrolly-map-container">
        <DVideo
          position={data.position}
          screenSize={screenSize}
          divIsFixed={divIsFixed}
          divIsAtBottom={divIsAtBottom}
          activeLayer={activeLayer}
          playFramesOnScroll = {playFramesOnScroll}
          videoPlayData ={videoPlayData.data}
          source ={videoPlayData.source}
        />

        <Spacer color={'transparent'} />

        <Waypoint
          onEnter={this.onEnterMapFromTop}
          onLeave={this.onExitMapFromTop}
        />


        {this.createWaypoints(data)}

        <Waypoint
          onEnter={this.onExitMapFromBottom}
          onLeave={this.onEnterMapFromBottom}
        />
    </div>
    ) 
  }
}

export default VideoWaypoint;




