// @flow

import React, { Component } from 'react';
import { Player, BigPlayButton, ControlBar } from 'video-react';
import "../../../../../../../node_modules/video-react/dist/video-react.css";
import './DVideo.css';



const getQueryString = (variable) => {
  var query = window.location.search.substring(1);
  var vars = query.split("&");
  for (var i=0;i<vars.length;i++) {
      var pair = vars[i].split("=");
      if(pair[0] == variable){
          return decodeURI(pair[1]);
      }
  }
  return false;
}



export default class DVideo extends Component{

  constructor(props) {
      super(props)
      this.state={ 
        divIsFixed: false.currentTime,
        frameNumber: 0,
        videoLoaded: false,
        isPlayingOnScroll: true,
        disableControlBar: true,
        showBigBtn: false,
        initialFramePlayed: false,
        muted: true
      }

  }


componentDidMount(){
    this.player.subscribeToStateChange(this.handleStateChange.bind(this));
}




componentWillUnmount() {
 // this.video.pause();
 if(this.props.divIsFixed){
  this.playInitialFrames()
 }

}


handleStateChange(state, prevState) {

  if(!this.state.initialFramePlayed ){
    if(state.currentTime >= 1 && state.currentTime < 3 ){

        this.setState({initialFramePlayed: true})
        this.player.pause();
        this.setState({showBigBtn: true, muted: false})
    }
  }
}



componentWillReceiveProps(props){
  if(this.props.divIsFixed !== props.divIsFixed  ){

    if(props.divIsFixed){
      this.playInitialFrames()
     }
  }
}




playInitialFrames=()=>{
  if(!this.state.initialFramePlayed ){
    this.player.play();
  }
}





getBigBtnClass=()=>{
  // if(this.state.showBigBtn){
  //   return "big-btn__force-show";
  // }else{
    return "big-btn__force-hide";
  //}
}


playBtnClicked=(e)=>{
  e.stopPropagation();
  this.player.seek(0)
  this.player.play();
  this.setState({showBigBtn: false, disableControlBar: false})

}




getVideoPlayer=(position)=>{

  const {source} = this.props;

  const playBtnStyle = {left : position === 'left'? 'inherit': '0%'};


  return(
    <div id="vh" className="video-holder"  >
      <Player
        ref={(player) => { this.player = player }} 
        playsInline
        poster={source.poster}
        src={source.video}
        muted={this.state.muted}
      >

      <ControlBar disableCompletely={this.state.disableControlBar} className="my-class" />
          <BigPlayButton position="center" className={this.getBigBtnClass()}  >  
          
          </BigPlayButton> 
      </Player>

      {this.state.showBigBtn && 
      <div className="playpause" style={playBtnStyle}>
            <img src="assets/playBtn.svg" onClick={this.playBtnClicked} /> 
        </div>
      }
    </div>
  )
}






  render(){
    const {divIsAtBottom, divIsFixed, playFramesOnScroll, position } = this.props;

    let height = '100%';

    if(!divIsFixed){
      height = 'inherit'
    }


    const videoStyle = {
      position: divIsFixed ? 'fixed' : 'absolute',
      top: divIsFixed && !divIsAtBottom ? '0px' : 'initial',
      bottom: divIsAtBottom ? '0px' : 'initial',
      height: height
      //display: screenSize === 'small' ? 'none' : 'initial'
    };


    return (
      <div style={videoStyle} id="dvideo"  >
          <div className="wrap">
              <div className="inner">
                  <div className="left">

                    {position === 'left' && this.getVideoPlayer('left')}

                  </div>

                  <div className="right">

                      {position === 'right' && this.getVideoPlayer('right')}

                  </div>
              </div>
          </div>
    </div>
      
    );
  }
}


